document.addEventListener('DOMContentLoaded', function(){
    createGaleria();
});

function createGaleria(){
    const galeria = document.querySelector('.galeria-imagenes');

    for ( let i = 1; i <= 12 ; i++ ) {
        const imagen = document.createElement('IMG');
        imagen.src = `build/img/thumb/${i}.jpg`;
        imagen.dataset.imagenID = i ;

        //AÃ±adir la funcion de mostrar imagen 

        imagen.onclick = mostrarImagen;
       
        const lista = document.createElement('LI');
        lista.appendChild(imagen);
        
        galeria.appendChild(lista);

    }

}

function mostrarImagen(e){

    const id = parseInt(e.target.dataset.imagenID);

    //Generar la imagen 
    const imagenG = document.createElement('IMG');
    imagenG.src = `build/img/grande/${id}.jpg`;
    
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagenG);
    overlay.classList.add('overlay');

    //Cerrar haciendo click en pantalla 

    overlay.onclick = function(){
        overlay.remove();
    }


    //Boton para cerrar la imagen 

    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent = 'X';
    cerrarImagen.classList.add('btn-cerrar');

    // On click cerrar imagen 
    cerrarImagen.onclick = function(){
        overlay.remove();
    }

    overlay.appendChild(cerrarImagen);

    //MOstrar en el html
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body');


    console.log(imagenG);
  
}



